<template>
  <div class="chatRoom">
    <div class="top" id="top">
      <h3></h3>
      <hr />
      <ul>
        <li v-for="(chat, index) in chatList" class="list-item" :key="index">
          <div class="userMessage">
            <span>{{ chat.userTel }}:</span>
            <span>{{ chat.message }}</span>
          </div>
        </li>
      </ul>
    </div>
    <div class="bottom">
      <el-input v-model="message" @keyup.enter.native="websocketsend" placeholder="" />
      <el-button @click.native="websocketsend"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      message: "",
      websock: null,
      chatList: [],
      websocketUrl:
      "ws://192.168.1.29:8080/webSocketServer/" + this.$store.state.userTel,
      receivedUserTels: this.checkedUserTels
    };
  },
  props: {
    //
    checkedUserTels: {
      type: Array
    }
  },
  methods: {

    initWebSocket() {
      //weosocket
      this.websock = new WebSocket(this.websocketUrl);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    //
    websocketonopen() {
      //send
      console.log("");
    },
    websocketonerror() {
      //
      console.log("");
      this.initWebSocket();
    },
    websocketonmessage(e) {
      //,,
      console.log("");
      let data = JSON.parse(e.data);
      if (data.message != null) {
        //
        this.chatList.push(data);
      } else {
        //,
        this.$emit("liveVideo",data);
      }
    },

    //
    websocketsend() {     
      switch(this.websock.readyState){
        case 0:
          this.$message("");
          return false;
        case 2:
          this.$message("");
          return false;
        case 3:
          this.$message("");
          return false;
      }
      if(this.receivedUserTels.length == 0){
        this.$message("");
      }else if(this.message == ""){
        this.$message("");
      }else{
        let data = {
        sendUserTel: this.$store.state.userTel,
        message: this.message,
        sendTime: new Date(),
        userTels:this.receivedUserTels.join(",")
        };
        this.websock.send(JSON.stringify(data));
      }

    },
    //eslint-ignore-next-line
    websocketclose() {
      console.log("");
    },
  },
  updated() {
    //
    let ele = document.getElementById("top");
    ele.scrollTop = ele.scrollHeight;
  },
  created() {
    this.initWebSocket();
  },
  destroyed() {
    this.websock.close(); //websocket
  }
      

};
</script>
<style scoped>
.chatRoom {
  position: fixed;
  width: inherit;
  margin-left: 10px;
  border-radius: 10px;
  background-clip: padding-box;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
ul {
  padding: 0px;
}
li {
  position: relative;
  line-height: 21px;
  word-break: break-all;
  word-wrap: break-word;
  margin-bottom: 8px;
  margin-top: 8px;
  font-size: 13px;
  outline: none;
  list-style: none;
  text-align: left;
}
.userMessage {
  vertical-align: middle;
}
span:first-child {
  color: red;
  margin: 0 5px;
}
span:nth-child(2) {
  color: black;
}
.top {
  height: 300px;
  overflow: auto;
}
.bottom {
  display: flex;
}
</style>
