/* eslint-disable */
<template>
  <div>
    <!--  -->
    <el-row :gutter="10">
      <el-col :span="8">
        <el-date-picker
          v-model="timeRange"
          type="daterange"
          align="right"
          unlink-panels
          range-separator=""
          start-placeholder=""
          end-placeholder=""
          format="yyyy  MM  dd "
          value-format="yyyy-MM-dd HH:mm:ss"
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-col>
      <el-col :span="2">
        <el-button type="success" @click="initVideos"></el-button>
      </el-col>
      <el-col :span="2">
        <el-button
          type="success"
          :disabled="checkedVideos.length == 0"
          @click="dialogVisible = true"
          ></el-button
        >
      </el-col>
      <el-col :span="2">
        <el-button
          type="success"
          :disabled="checkedVideos.length == 0"
          @click="synVideos"
          ></el-button
        >
      </el-col>
      <el-col :span="2" >
        <el-button
          type="success"
          :title="patten ? '' : ''"
          icon="el-icon-s-operation"
          @click="patten = !patten"
        ></el-button>
      </el-col>
    </el-row>
    <!--  -->
    <div v-if="patten">
      <el-row v-show="recordVideoList.length != 0" :gutter="20">
        <el-checkbox
          :indeterminate="isIndeterminate"
          v-model="checkAll"
          @change="handleCheckAllChange"
          ></el-checkbox>
        <el-checkbox-group v-model="checkedVideos">
          <draggable :list="recordVideoList" class="list-group" :animation='200'>
          <el-col
            :span="6"
            v-for="video in recordVideoList"
            :key="video.videoId"
          >
          <!--  -->
            <VideoPlayer
              :ref="`video${video.videoId}`"
              :options="{
                controls: true,
                autoplay: true,
                muted: true,
                fluid: true,
                sources: [{ src: video.videoUrl, type: 'rtmp/flv' }]
              }"
            ></VideoPlayer>
            <VideoInfo :videoInfo="video"></VideoInfo>
            <el-checkbox :label="video">
              <!-- ,label -->
              <div style="display:none">1</div>
            </el-checkbox>
          </el-col>
          </draggable>
        </el-checkbox-group>
      </el-row>
    </div>
    <!--  -->
    <div v-else>
      <VideoMap :videoPoints="recordVideoList"></VideoMap>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-input v-model="title" placeholder=""></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addTitle"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
//eslint-disable-next-line
import { getRequest, postRequest,dateToString} from "../../utils/api";
import VideoPlayer from "@/components/VideoPlayer.vue";
import VideoMap from "@/components/VideoMap.vue";
import VideoInfo from "@/components/VideoInfo.vue";
import draggable from "vuedraggable"
export default {
  data() {
    return {
      patten: true, //true,flase
      title: "",
      dialogVisible: false,
      isIndeterminate: true,
      checkAll: false,
      checkedVideos: [],
      recordVideoList: [],
      //
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      timeRange: ""
      
    };
  },
  components: {
    VideoPlayer,
    VideoMap,
    VideoInfo,
    draggable
  },
  methods: {
    handleCheckAllChange(val) {
      this.checkedVideos = val ? this.recordVideoList : [];
      this.isIndeterminate = false;
    },
    addTitle() {
      //eslint-disable-next-line
      let _this = this;
      let ids = this.checkedVideos.map(video => video.videoId).join();
      let data = {
        ids: ids,
        title: this.title
      }
      getRequest("/web/addTitle",data).then(
        resp => {
          resp.data.statusCode == 200?this.$message({message:resp.data.msg,type:'success'}):this.$message({message:resp.data.msg,type:'error'});
          //
          _this.title = "";
          _this.initVideos();
        }
      );
      this.dialogVisible = false;
    },
    //
    initVideos() {
      let _this = this;
      //,
      if(!this.timeRange){
        let startTime = dateToString()+"00:00:00";
        let endTime = dateToString()+"23:59:59";
        this.timeRange = [startTime,endTime];
      }
      let video = {
          startTime: this.timeRange[0],
          endTime: this.timeRange[1]
        };
      postRequest("/web/latestVideos", video).then(
        resp => {
          _this.recordVideoList = resp.data;
          // _this.recordVideoList.splice(0, _this.recordVideoList.length); //
          //vue?
          //
          // what fuck!
          // for (let item of resp.data) {
          //   //,,push
          //   _this.recordVideoList.push(item);
          // }
        },
        resp => {
          console.log(resp.data);
        }
      );
    },
    synVideos(){
      let latestTime = this.getLatestTime(this.checkedVideos);
      this.checkedVideos.forEach(video => {
        //[0]
        this.$refs[`video${video.videoId}`][0].setCurrentTime(new Date(video.startTime),latestTime)
      });
    },
    getLatestTime(videos){
      let timeList = videos.map(video => {
        return new Date(video.startTime);
      })      
      let latestTime = timeList.reduce((time1,time2) => {
        return time1>time2?time1:time2;
      })
      return latestTime;
    }
  },
  mounted() {
    this.initVideos();
  }
};
</script>

<style>
.el-col {
  border-radius: 20px;
  margin-bottom: 20px;
}
</style>
